@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500&display=swap);
* {
	font-family: "Poppins", sans-serif;
	margin: 0;
	padding: 0;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
		monospace;
}

.App {
	text-align: center;
	text-align: center;
	margin: 3% 20vw;
}
.app-wrapper {
	margin: 0;
	margin-top: 0px !important;
	margin: 5vh 5% 0px;
}
h1 {
	margin-bottom: 20px;
}
p {
	text-align: left;
	margin-bottom: 20px;
}
h3 {
	margin-bottom: 15px;
}
.expand-icon {
	border-radius: 50%;
	padding: 10px;
}
.fontret {
	font-family: "Poppins", sans-serif !important;
	font-weight: 570 !important;
	text-align: center;
	padding: 16px;
}
.expand-icon:hover {
	background: rgb(236, 234, 234);
}
.App-header {
	background-color: #282c34;
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	font-size: calc(10px + 2vmin);
	color: white;
}

.App-link {
	color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
	from {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	to {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

@keyframes App-logo-spin {
	from {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	to {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}
@media screen and (max-width: 500px) {
	h3 {
		font-size: 5vw;
	}
	h1 {
		font-size: 6vw;
		margin-bottom: 10px;
		font-weight: 580;
	}
	.App {
		margin: 3% 15vw;
	}
}

.nav-bar {
	display: flex;
	justify-content: space-between;
	margin: auto;
	width: 90%;
	/* position: absolute; */
	top: 0%;
	align-items: center;
	font-size: 1vw;
}

.nav-bar main {
	display: flex;
	justify-content: space-around;
	width: 30%;
}
.nav-bar aside {
	display: flex;
	justify-content: space-around;
	min-width: 20vw;
	align-items: center;
}
.image {
	width: 6vw;
	height: auto;
}

.btn-text {
	/* padding: 0; */
	margin: 0;
	font-size: 1vw;
}

@media screen and (max-width: 1000px) {
	.nav-bar {
		font-size: 1.5vw;
	}
	.nav-bar main {
		width: 40%;
	}
}

@media screen and (max-width: 500px) {
	.nav-bar {
		font-size: 2vw;
	}
	.nav-bar main {
		width: 50%;
	}

	.nav-bar aside {
		width: 30%;
	}
}

.accordian {
	box-shadow: 0px 2px 1px -1px rgb(0 0 0 / 20%),
		0px 1px 1px 0px rgb(0 0 0 / 14%), 0px 1px 3px 0px rgb(0 0 0 / 12%);
	margin-bottom: 30px;
}

.accordian-axs {
	padding: 12px;
}

.accordian-details-style {
	margin-left: 3%;
	padding-bottom: 24px;
	padding: 16px;
}

.accordian-details-style ol li {
	padding-right: 30px;
	margin: 20px;
}

.accordian-details-style ul li {
	margin: 20px;
}

